@import url(https://fonts.googleapis.com/css2?family=Kufam:wght@400;600&family=Rammetto+One&display=swap);
* {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
}

body {
	text-align: center;
	margin: 0 auto;
	font-family: "Kufam", cursive;
	background-color: #fe7171;
	color: #000;
	height: 100vh;
}

#root {
	height: 100%;
}

#heading {
	font-family: "Rammetto One", cursive;
	margin-bottom: 15px;
	margin-top: 100px;
}

@media only screen and (max-width: 480px) {
	#heading {
		font-size: 6vw;
	}
}

.SetTimer-Buttons {
    display: flex;
    flex-direction: row;
    align-items: baseline;
}


.SetTimer-Buttons > button {
    background-color: #799351;
    color: #fff;
    border-radius: 3px;
    border: none; 
    padding: 10px 25px;
    margin: 4px 15px;
}


@media only screen and (max-width: 480px) {

    .SetTimers {
        margin: 10px 0;
    }

    .SetTimer-Buttons {
        justify-content: center;
    }
}    
.Timer > #timer-label, #time-left {
    margin: 0;
}

.Controls > button {
    background-color: #799351;
    color: #fff;
    border-radius: 3px;
    border: none;
    padding: 10px 25px;
    margin: 4px 5px;
}

button:hover {
    background-color: #fe7171;
    outline: 1px solid #fff;
    outline-offset: -1px;
    cursor: pointer;
}

i {
    font-size: 20px;
}
footer {
	margin-top: 20px;
	font-size: 14px;
	color: #333;
	width: 100%;
}

a {
	color: #333;
	text-decoration: none;
}

a:hover {
	color: #fff;
}

/* media queries for mobile devices */
@media only screen and (max-width: 480px) {
	footer {
		font-size: 12px;
	}
}

.app-container {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	width: 100%;
	height: auto;
	margin: 0 auto;
}

.app-wrapper {
	display: flex;
	flex-direction: column;
	justify-content: center;
	padding: 40px;
	font-weight: bold;
	background-color: #ffa36c;
	border: 5px solid #799351;
	box-shadow: 0 10px 30px #993333;
	border-radius: 25px;
	max-width: 730px;
	min-width: 400px;
	width: 90%;
}

.timers-wrapper {
	display: flex;
	flex-direction: row;
	justify-content: space-around;
	margin-bottom: 40px;
}

@media only screen and (max-width: 480px) {
	.app-wrapper {
		min-width: 300px;
	}

	.timers-wrapper {
		display: flex;
		flex-direction: column;
	}
}

